<template>
    <div>
        <div class="item-image">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item,index) in detailsData.images" :key="index">
                        <img :src="item" alt="">
                    </div>
                </div>
            </div>

        </div>

        <div class="item-ele">
            <div class="ele-title">{{detailsData.title}}</div>
            <div class="ele-price">¥{{detailsData.price}}</div>
            <div class="ele-wrap">
                <div>快递：{{detailsData.freight}}元</div>
                <div>月销量{{detailsData.sales}}件</div>
            </div>
        </div>

        <div class="item-reviews">
            <div class="reviews-title">商品评价（{{total}}）</div>
            <div v-show="reviewdata.length>0">
                <div class="reviews-wrap">
                    <div class="reviews-list" v-for="(item, index) in reviewdata" :key="index">
                        <div class="uinfo">
                            <div class="head">
                                <img data-v-a304f74e="" :alt="item.title" :src="item.head">
                            </div>
                            <div class="nickname">{{item.nickname}}</div>
                        </div>
                        <div class="reviews-content">{{item.content}}</div>
                        <div class="reviews-data">{{item.times}}</div>
                    </div>
                </div>
            </div>
            <div data-v-0cb1cf6a="" class="no-data" v-show="reviewdata.length<=0" style="">暂无评价！</div>
        </div>
        <div data-v-a304f74e="" class="bottom-btn-wrap">
            <div data-v-a304f74e="" class="btn fav" @click="addFav">收藏</div>
            <div data-v-a304f74e="" class="btn cart" @click="onShowpanelStyle">加入购物车</div>
        </div>
        <div data-v-a304f74e="" class="mask" v-show="isShowPlnel" @click="onHidepanelStyle" style=""></div>
        <div ref="cart-panel" :class="isShowPlnel?'cart-panel up':'cart-panel down'">
            <div data-v-a304f74e="" class="goods-info" ref="goods-info">
                <div data-v-a304f74e="" class="close-panel-wrap">
                    <div data-v-a304f74e="" class="spot"></div>
                    <div data-v-a304f74e="" class="line"></div>
                    <div data-v-a304f74e="" class="close"></div>
                </div>
                <div data-v-a304f74e="" ref="goods-img" class="goods-img"><img data-v-a304f74e=""
                                                                               :src="detailsData.images&&detailsData.images[0]"
                                                                               alt=""></div>
                <div data-v-a304f74e="" class="goods-wrap">
                    <div data-v-a304f74e="" class="goods-title">{{detailsData.title}}</div>
                    <div data-v-a304f74e="" class="price">¥{{detailsData.price}}</div>
                    <div data-v-a304f74e="" class="goods-code">商品编码:{{detailsData.gid}}</div>
                </div>
            </div>
            <div data-v-a304f74e="" class="attr-wrap">
                <div data-v-a304f74e="" class="attr-list" v-for="(item,index) in specsdata" :key="index">
                    <div data-v-a304f74e="" class="attr-name">{{item.title}}</div>
                    <div data-v-a304f74e="" class="val-wrap">
                        <span data-v-a304f74e="" @click="SPECS_STYLE({index:index,index2:index2})"
                              :class="{val:true, active:item2.active}"
                              v-for="(item2,index2) in item.values"
                              :key="index2">{{item2.value}}</span>
                    </div>
                </div>

            </div>
            <div data-v-a304f74e="" class="amount-wrap">
                <div data-v-a304f74e="" class="amount-name">购买数量</div>
                <div data-v-a304f74e="" class="amount-input-wrap">
                    <div data-v-a304f74e="" :class="count>1?'btn dec':'btn dec active'" @click="count>1?--count:1">-
                    </div>
                    <div data-v-a304f74e="" class="amount-input"><input data-v-a304f74e="" type="number"
                                                                        @input="setAmount($event)" :value="count"></div>
                    <div data-v-a304f74e="" class="btn inc" @click="++count">+</div>
                </div>
            </div>
            <div data-v-a304f74e="" class="sure-btn" @click="onsubmit">确定</div>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapState, mapMutations} from "vuex"
    import Vue from 'vue';
    import {Toast} from 'vant';

    Vue.use(Toast);
    import Swiper from "swiper"
    import TweenMax from "../../assets/js/libs/TweenMax"

    export default {
        data() {
            return {
                gid: this.$route.query.gid ? this.$route.query.gid : "",
                isShowPlnel: false,
                count: 1
            }
        },
        mounted() {
            //网页头部变化
            document.title = this.detailsData.title;
        },
        created() {
            this.isMove = true;
            //网页头部变化
            document.title = this.detailsData.title;
            //商品规格
            this.getspecs({gid: this.gid});
            //商品详情
            this.getdetails({
                gid: this.gid, success: () => {
                    this.$nextTick(() => {
                        new Swiper('.swiper-container', {
                            autoplay: 3000,//可选选项，自动滑动

                        })
                    })
                }
            });
            //商品评价
            this.getReview({gid: this.gid});

        },
        computed: {

            ...mapState({
                detailsData: (state) => state.goods.detailsData,
                //商品评价
                reviewdata: (state) => state.goods.reviewdata,
                //评价数量
                total: (state) => state.goods.total,
                //商品规格
                specsdata: (state) => state.goods.specsdata,
                //登录权限
                isLogin:state=>state.user.isLogin
            })
        },
        methods: {
            ...mapMutations({
                //样式改变商品规格
                SPECS_STYLE: "goods/SPECS_STYLE",
                //购物车数据
                SET_GOODS_DATA:"cart/SET_GOODS_DATA"
            }),
            ...mapActions({
                getdetails: "goods/getdetails",
                //商品评价
                getReview: "goods/getReview",
                //商品规格
                getspecs: "goods/getspecs",
                //商品加入收藏
                getAddFav:"goods/getAddFav"
            }),
            //计算数量
            setAmount(e) {
                this.count = e.target.value;
                this.count = this.count.replace(/[^\d]/g, "");
                if (!this.count || this.count === "0") {
                    this.count = 1;
                }
            },
            //显示面板
            onShowpanelStyle() {
                this.isShowPlnel = true
            },
            //隐藏面板
            onHidepanelStyle() {
                if (this.isMove) {
                    this.isShowPlnel = false
                }

            },
            //提交购物车
            onsubmit() {
                //检测是否选中
                if (this.specsdata.length > 0) {
                    let isActive = false;
                    for (let i = 0; i < this.specsdata.length; i++) {
                        isActive = false;
                        for (let j = 0; j < this.specsdata[i].values.length; j++) {
                            if (this.specsdata[i].values[j].active) {
                                //选中的时候为true
                                isActive = true;
                                break;
                            }
                        }

                        if (!isActive) {
                            Toast("请你选择" + this.specsdata[i].title);
                            break;
                        }

                    }
                    //提交到购物车
                    if (isActive) {
                        this.addCartSumit()
                    }
                }
            },
            //添加购物车
            addCartSumit() {

                if (this.isMove) {
                    this.isMove = false;
                    //动画效果
                    let goodsImg = this.$refs["goods-img"], goodsInfo = this.$refs["goods-info"];
                    //获取购物车图标的id
                    let detailsCart = document.getElementById("details-cart");
                    //购物车面板和整个窗体的高度差
                    let panelSidel = window.innerHeight - this.$refs["cart-panel"].offsetHeight;
                    //克隆节点
                    let cloneGoodImg = goodsImg.cloneNode(true);
                    //设置CSS
                    // cloneGoodImg.style.cssText("");
                    cloneGoodImg.style.cssText = "position:absolute;z-index:10;left:0.2rem;top:0.2rem;width:0.4rem;height:0.4rem;"
                    //放入节点
                    goodsInfo.appendChild(cloneGoodImg);
                    //
                    TweenMax.to(cloneGoodImg, 3, {
                        bezier: [{x: cloneGoodImg.offsetLeft, y: -100}, {x: detailsCart.offsetLeft, y: -panelSidel}],
                        onComplete: () => {
                            //alert("动画执行完毕")
                            //移除节点
                            cloneGoodImg.remove();
                            //完成可以隐藏
                            this.isMove = true;
                            //加入购物车
                            let attrs = []  //商品规格
                                , param = [];
                            if (this.specsdata.length > 0) {
                                for (let i = 0; i < this.specsdata.length; i++) {
                                    param = [];
                                    for (let j = 0; j < this.specsdata[i].values.length; j++) {
                                        if (this.specsdata[i].values[j].active){
                                            param.push({
                                                paramid: this.specsdata[i].values[j].vid,
                                                title: this.specsdata[i].values[j].value
                                            })
                                        }
                                    }
                                    attrs.push({
                                        attrid: this.specsdata[i].attrid,
                                        title: this.specsdata[i].title,
                                        param: param
                                    })
                                }

                            }
                            //总数据
                            let goodsDatas = {
                                gid: this.gid,
                                title: this.detailsData.title,
                                amount: this.count,
                                price: this.detailsData.price,
                                img: this.detailsData.images[0],
                                checked: true,
                                freight: this.detailsData.freight,
                                attrs: attrs

                            };
                        this.SET_GOODS_DATA({goodsDatas:goodsDatas});

                        }
                    });
                    //设置效果
                    TweenMax.to(cloneGoodImg, 0.2, {rotation: 360, repeat: -1});
                }

            },
            //加入收藏
            addFav(){
                if (this.isLogin){
                    this.getAddFav({gid:this.gid,success:(res)=>{
                            Toast(res.data)
                        }})
                } else {
                    Toast('请登录会员')
                }

            }
        },
        beforeRouteUpdate(to, form, next) {
            //网页头部变化
            document.title = this.detailsData.title;

            this.getdetails({
                gid: to.query.gid, success: () => {
                    this.$nextTick(() => {
                        new Swiper('.swiper-container', {
                            autoplay: 3000//可选选项，自动滑动
                        })
                    })
                }
            });
            next()
        }
    }
</script>

